import React from 'react'
import FeaturesData from './FeaturesData'
import Accordian from '../accordian/Accordian'

export default function HostingFeatures() {

  

  return (
    <>
      <div className="w-full bg-[#93C5FD] pt-5 pb-7 my-[5em]">
        {FeaturesData.map((data , i) => (
          <div className="px-5 mx-auto md:w-[80%] pt-5">
            <h3 className='text-[1.4em] md:text-[2.5em] font-bold border-b-[4px] mb-5 border-l-[4px] border-[#2E3B80] md:w-[700px] pl-3 text-[#2E3B80]'>{data.title}</h3>
            <p className='text-[1em] md:text-[1.2em] text-center'>{data.detail}</p>

            <div className="">
              <div className="card md:flex md:w-[100%] justify-between items-start">
                <div className='md:w-[45%]'>
                  <ul>
                    {data.subli.map(data =>
                      <div className=""><li className='my-5 text-[1.2em] text-white'><h3 className='text-[1.2em] font-bold'>{data.title}</h3> {data.detail}</li>

                      </div>
                    )}
                  </ul>
                </div>
              </div>
            </div>
            <p className='text-[1em] md:text-[1.2em] text-center'>{data.detailli}</p>

            <div className="bg-[#2E3B80] p-5 rounded text-white">
             
              {data.otherfeatures.map(data =>
                <div >
                   <ul>
                <li className='my-5 text-[1.2em] text-white'><h3 className='text-[1.2em] font-bold'>{data.title}</h3> {data.detail}</li>
                </ul>
                  <div className="w-full flex justify-between flex-wrap gap-2">
                    {data.tabledata.map(data =>
                      <div className="md:w-[48%]">
                        <div className='border my-2 p-4'>
                         <h3 className='font-bold underline pb-2'> {data.title}</h3>
                          <div className="">
                            <p className='font-bold'>{data.detail}</p>
                          </div>
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              )}
            </div>
            <p className='text-[1em] md:text-[1.2em] text-center my-4'>{data.subdetail}</p>
            <p className='text-[1em] md:text-[1.2em] text-center'>{data.comment}</p>
        
                      <div className="">
                        <Accordian title="Bluehost CPU Protection"
                         detail="Bluehost employs a policy called CPU Protection for all plans that use shared hosting. When you 
                         use shared hosting, it means that your site is not the only one on a given server. That’s why 
                         Bluehost (and other providers) can offer web hosting so cheaply: no one is paying to run their
                          own server." 
                          
                          subdetail="However, this does mean that all the sites on a given server need equal 
                          access to hardware resources. That’s where CPU Protection comes in. If your site makes excessive
                           demands on the server, then it is deliberately denied access to extra hardware resources.
                            The end result is that your site may become inaccessible, or incredibly slow." 
                            
                            detailthree="The good news is, it’s rare to end up the target of CPU Protection. If you are running a
                             local news blog, however, and traffic suddenly spikes because of an important story, then you might
                              have an issue. CPU Protection can also come into play if you have a badly coded bit of JavaScript 
                              running wild. For the most part, however, CPU throttling should not be something to worry about."
                               index="i"/>
                      </div>
            
            <div className="bg-[#2E3B80] p-5 rounded text-white mt-4">
             
             {data.further.map(data =>
               <div >
                  <ul>
               <li className='my-5 text-[1.2em] text-white'><h3 className='text-[1.2em] font-bold'>{data.title}</h3></li>
               </ul>
                 <div className="w-full flex justify-between flex-wrap gap-2">
                   {data.furtherinfodetail.map(data =>
                     <div className="md:w-[48%]">
                       <div className='border my-2 p-4'>
                        <h3 className='font-bold underline pb-2'> {data.title}</h3>
                         <div className="">
                           <p className='font-bold'>{data.detail}</p>
                         </div>
                       </div>
                     </div>
                   )}
                 </div>
               </div>
             )}
           </div>
           
            
          </div>

  

        ))}
      </div>
    </>
  )
}
