import React from 'react'
import CustomerData from './CustomerData'

export default function Customer() {
    return (
        <>
             <div className=" w-full bg-[#93C5FD] pt-5 pb-7 mb-[5em]">
                {CustomerData.map(data =>
                     <div className="px-5 mx-auto md:w-[80%] pt-5">
                         <h3 className='text-[1.4em] md:text-[2.5em] font-bold text-[#2E3B80]'>{data.title}</h3>
                         <p className='text-[1em] '>
                            {data.detail}
                        </p>
                        <img src={data.img} className='mx-auto' alt="" />
                        <div>
                            <span className='text-[12px]'>{data.smalltext}</span>
                            <p className='text-[1em] '>{data.detailmore}</p>
                            <p className='text-[1em]'>{data.detaillast}</p>
                        </div>

                        <div>
                        <h3 className='text-[1.4em] md:text-[2.5em] font-bold text-[#2E3B80]'>{data.limitationstitle}</h3>
                        <p className='text-[1em]'>{data.limitationdetail}</p>
                        </div>
                    </div>

                )}
            </div>
        </>
    )
}
