import React from 'react'
import FeatursData from './FeatursData'

export default function Featurs() {
  return (
    <>
         <div className="my-[5em]">
            {FeatursData.map(data =>
             <div className="px-5 mx-auto md:w-[80%]">
                <h3 className='text-[1.4em] md:text-[2.5em] font-bold  text-[#2E3B80] text-center'>{data.title}</h3>
                <p className='text-[1em] text-center py-4'>{data.detail}</p>
                <p className='text-[1em] text-center'>{data.subdetail}</p>

                <ul>
           {data.list.map(data=>
            <li className='my-5 text-[1.2em]  items-center'><h3 className='text-[1.2em] font-bold md:mr-4'>{data.title}</h3> {data.detail}</li>
            )}
          </ul>
          <h3 className='text-[1.4em] md:text-[2.5em] font-bold  text-[#2E3B80] text-center'>{data.titletwo}</h3>
          <p className='text-[1em] text-center py-4'>{data.detailtwo}</p>
          <p className='text-[1em] text-center'>{data.detailthree}</p>
          <ul>
           {data.further.map(data=>
            <li className='my-5 text-[1.2em]  items-center'><h3 className='text-[1.2em] font-bold md:mr-4'>{data.title}</h3> {data.detail}</li>
            )}
            </ul>
           </div>
            )}
        </div>
    </>
  )
}
