import React from 'react'
import SecurityData from './SecurityData'

export default function Security() {
  return (
    <>
       <div className=" w-full bg-[#93C5FD] pt-5 pb-7 mb-[5em]">
        {SecurityData.map(data => 
       <div className="px-5 mx-auto md:w-[80%] pt-5">
       <div className="">
       <h3 className='text-[1.4em] md:text-[2.5em] font-bold border-b-[4px] mb-5 border-l-[4px] border-[#2E3B80] md:w-[350px] pl-3 text-[#2E3B80]'>{data.title}</h3>
     
       <p className='text-[1em] md:text-[1.2em] text-white'>{data.detail}</p>
        </div>
        </div>
        )}
      </div>
    </>
  )
}
